<template>
  <div class="g-side">
    <el-tree
      :data="data"
      :props="defaultProps"
      node-key="id"
      :default-expanded-keys="[1]"
      accordion
      highlight-current
      @node-click="handleNodeClick"
      class="m-eltree">
    </el-tree>

  </div>
</template>

<script>
  export default{
    name:'NavLeft',
    data(){
      return {
         data:[{
           id:1,
           label: 'css',
           children:[{
               label: 'css_center:水平垂直',
               url:'cssCenter',
           },{
               label: 'css_center_flex:水平垂直居中',
               url:'cssCenterFlex',
           },{
               label: 'css_center_table:水平垂直居中',
               url:'cssCenterTable'
           },{
               label: 'css_center_text:文字水平垂直居中'
           },{
               label: 'css_overflow:左列固定，右列自适应'
           }]
         },{
           label:'html',
           children:[{
               label:'html'
          }]
         }],
        defaultProps:{
          children:'children',
          label:'label'
        }
      }
    },
    methods:{
      handleNodeClick(data) {
          if(data.children==undefined){
            this.$router.push({path: data.url });
          }
        }

    }
  }

</script>

<style lang="less" src="../less/NavLeft.less">


</style>



