import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import cssCenter from '@/components/cssCenter.vue'
import cssCenterFlex from '@/components/cssCenterFlex.vue'
import cssCenterTable from '@/components/cssCenterTable.vue'

const routes=[
  {
    path:'/cssCenter',
    name:'cssCenter',
    component:cssCenter
  },{
    path:'/cssCenterFlex',
    name:'cssCenterFlex',
    component:cssCenterFlex
  },{
    path:'/cssCenterTable',
    name:'cssCenterTable',
    component:cssCenterTable
  }
]
export default routes



